
.Whole
{
  z-index: 1;
  width: 100vw;
  height: 100%;
  
  background-image: linear-gradient(to bottom right,violet,aqua);
  display: flex;
  flex-direction: column;

}
.TopBar
{
  padding: min(50px);
  background-color: transparent;
  opacity: 1;
  display: flex;
  justify-content: space-around;
}
button{
  min-width: 15rem;
  opacity: 0.5;
  background-color: transparent;
  border-color: transparent;
  border: 10cm;

}

.poza
{
  --inaltime: 22rem;
  position: relative;
  height: var(--inaltime);
  width: calc(3/2*var(--inaltime));
  border-radius: 2rem;
  -webkit-transition: border-size .5s ease;
  -moz-transition: border-size .5s ease;
  -o-transition: border-size .5s ease;
  transition: border-size .5s ease;
  transition: border-radius 0.6s linear;
  -moz-transition: height 1s ease;
  -webkit-transition: height 1s ease;
  -o-transition: height 1s ease;
  transition: height 1s ease;
  -moz-transition: width  1s ease;
  -webkit-transition:width  1s ease;
  -o-transition:width 1s ease;
  transition:width  1s ease;
  transition: all .2s ease-in-out;
  border-style: solid;
  box-shadow: 1cm;
  border-width: 0.2cm;
  border-color: black;
}
.poza:hover
{
  z-index: 3;
  transform: scale(1.1);
  border-radius: 0.5rem;
}
.TopButton
{
  font-family: 'Dosis', serif;
  font-weight: bold;
  font-size: x-large;
  transition: all .2s ease-in-out;
  
}
button:hover
{
  opacity: 1;
  transform: scale(1.6);
 
}
.Logo
{
  font-size: xx-large;
  opacity: 1;
  transform: scale(1.6);
  font-family: 'Josefin Slab';
}
.PozeFlex
{
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-direction: row;
  width: 100%;
  height: fit-content;

  row-gap: 3rem;
}
.BottomBar
{
  display: flex;
  position: relative;
  justify-content: space-between;
  bottom: 0%;
  height: 5rem;
  width: 100vw;
  background-image: linear-gradient(to bottom left,violet,aqua);
  z-index: 4;
  border-bottom: 0;
  border-left: 0;
  border-right: 0;
  border-style: ridge;
  border-color: aqua;
  margin-top: 3rem;
}
.Contact-Stanga
{ display: flex;
  flex-direction: row;
  left: 0;
  height: 5rem;
  margin-top: auto ;
  margin-bottom: auto; 
}
.Contact-Dreapta
{ display: flex;
  flex-direction: row;
  right: 0;
  height: 5rem;
  margin-top: auto ;
  margin-bottom: auto; 
}
.Icoane
{
  margin-top: auto ;
  margin-bottom: auto;
  margin-left: 2rem;
  margin-right: 2rem;
}
.Scris
{
  margin-top: auto ;
  margin-bottom: auto;
  font-family:  'Dosis', serif;

}
a {
  text-decoration: none;
  color: black;
 } 